import React from 'react';

import classes from './Burger.css';
import BurgerIngredient from './BurgerIngredient/BurgerIngredient';

const burger = (props) => {
    let transformedIngredient = Object.keys(props.ingredients).map(igrkey => {
        return [...Array(props.ingredients[igrkey])].map((_, i) => {
            return <BurgerIngredient key={igrkey + i} type={igrkey} />
        });
    }).reduce((arr, el) => {
        return arr.concat(el);
    });

    if (transformedIngredient.length === 0) {
        transformedIngredient = <p>Please choose your <big>ingredients!</big></p>
    }

    return (
        <div className={classes.Burger}>

            <BurgerIngredient type='bread-top' />
            {transformedIngredient}
            <BurgerIngredient type='bread-bottom' />
        </div>
    )
}

export default burger


